:root {
    --evening:#192231;
    --sunset:#985E6D;
    --cloud:#98878F;
    --stormy:#494E6B;
    --teal:#56f4de;
}

body{
   background-color: var(--evening);
   color: white;
   margin: 0;
}

input, select{
    padding: .3em;
    border-radius: 4px;
    margin-right: 1em;
    max-height: 15px;
}

label{
   margin: .5em;
}

button{
  font-size: 1.2em;
}
.loading-text{
  text-align: center;
  font: 16px "Fira Sans", sans-serif;
  margin-top: 40%;
  color: var(--stormy);
}

.user-name{
  font: 16px "Fira Sans", sans-serif;
}
.quick-message{
  margin: 10px;
  font-size: 18px;
}

.header{
  display:flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: baseline;
  background-color: var(--stormy);
  margin: 0px;
}

.main{
   display: flex;
   flex-direction: column;
   flex-wrap: wrap;
   font: 14px "Fira Sans", sans-serif;
   align-items: center;
}

.sideMenu{
   margin-top: 20px;
   margin-left: 10px;
   display: flex;
   flex-direction: column;
   font-size: 1em;
}

.sideMenu button{
   margin: .6em;
}

.dashboard{
   display: flex;
   flex-direction: column;
   flex-wrap: wrap;
   align-items: center;
   max-width: 485px;
   margin-right: 10px;
   margin-left: 10px;
}

.dashboard>*{
   margin-top: .5em;
   margin-bottom: .5em;
}

.main-top{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
}

.quick-menu{
   display: flex;
   flex-direction: column;
   justify-content: center;
   padding: .25em;
}

.main p{
   text-align: center;
   margin-right: 25px;
}

li{
   list-style-type: none;
   font-size: 14px;
}

.employee-name{
   padding: .5em;
   border-left: 1px solid var(--teal);
   font-size: 20px;
}

.employee-name:hover{
   background-color: var(--stormy);
   color: white;
}

.MuiPaper-root{
   background: linear-gradient(90deg, rgba(25,34,49,1) 0%, rgba(73,78,107,1) 100%, rgba(152,135,143,1) 100%);
}

.MuiTextField-root{
   background: white;
   border-radius: 4px;
   max-width: 120px;
}

.MuiInputBase-root{
   background: white;
   border-radius: 4px;
   max-width: 120px;
   max-height: 45px;
   padding: 5px;
}

.MuiList-root, .MuiTypography-root, .MuiPickersCalendarHeader-label, .MuiDayPicker-weekDayLabel, .MuiButton-root{
   color: white;
}

.MuiSvgIcon-root{
   color: var(--teal);
}

.MuiDataGrid-columnHeaders{
   color: var(--teal);
}

.report-container{
   display: flex;
   justify-content: space-between;
}

h4, h5, h3  {
   margin: .5em;
   margin-left: 0;
   color:var(--teal);
   font-weight: 100;
}

h1{
  text-align: center;
  margin-bottom: 5px;
  margin-top: 5px;
  margin-right: 2%;
  margin-left: 5px;
}

table{
   width: 100%;
   margin: .5em;
   border-spacing: 0 20px;
}

form{
  display: flex;
  flex-direction: column;
}

.modal-bg{
  margin: 0;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.7);
  z-index: 100;
}

.modal{
  width: 100vw;
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  background: linear-gradient(90deg, rgba(25,34,49,1) 0%, rgba(73,78,107,1) 100%, rgba(152,135,143,1) 100%);
  color: white;
}

.modal-content form>*{
   margin: 1em;
}

.buttons{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items: flex-end;
   margin-top: 2em;
}

.clock-in-out{
   display: flex;
   flex-direction: row;
   margin-bottom: .5em;
   justify-content: center;
}

.quick-menu-shortcuts{
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content: center;
}

.quick-menu h4{
   text-align: center;
}

.quick-menu-shortcuts button:hover, .header-btn:hover, .edit-button:hover, .apply-btn:hover{
  background-color: var(--teal);
  color: var(--evening);
  background: var(--teal);
}

.details-card{
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   padding: .5em;
   color: white;
   width:100;
}

.details-card>*{
   margin: 5px;
}

.details-card li{
  margin-bottom: 10px;
  margin-top: 10px;
}

.buttons >*{
   margin: 5px;
}

.quick-menu button{
   margin: 6px;
}

.quick-menu-header{
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
  align-items: baseline;
  justify-content: flex-end;
  border-radius: 3px;
  background: linear-gradient(90deg, rgba(25,34,49,1) 0%, rgba(73,78,107,1) 100%, rgba(152,135,143,1) 100%);
}

 .details-btn{
   justify-content: flex-end;
   border: none;
 }

th{
   text-align: left;
   border-right: solid black 1px;
   font-weight: normal;
   color: var(--teal);
   font-size: 14px;
}

.edit-shift{
   text-align: center;
   color: var(--teal);
}

tr{
   font-size: 14px;
   margin-bottom: 10px;
}

button{
   padding: .5em;
   background: var(--teal), .5;
   color: var(--teal);
   border-radius: 4px;
   border: 1px solid var(--teal);
   font-size: 14px;
}

 button:hover{
    transition: 500ms;
}

button:active{
   background-color: var(--teal);
}

button:disabled{
   color: var(--stormy);
   border: 1px solid var(--stormy);
}

.edit-form{
   background-color: var(--midnight);
   display: flex;
   justify-content: center;
   border-radius: 4px;
   max-width: 150px;
   align-self: center;
}

.edit-form input{
   text-align: center;
   background-color: white;
   max-height: 20px;
}

.edit-button{
   color: var(--teal);
}

.MuiClockNumber-root, .PrivatePickersToolbarText-root{
   color: white;
}

.clock-in-btn{
   min-height: 5.5em;
   min-width: 100px;
   background: linear-gradient(129deg, rgba(118,201,76,1) 55%, rgba(195,255,164,1) 100%);
   color: var(--evening);
   border: 1px solid rgba(118,201,76,1);

}
.clock-out-btn{
   min-height: 5.5em;
   min-width: 100px;
   background: linear-gradient(145deg, rgba(191,81,73,1) 70%, rgba(255,166,166,1) 100%);
   color: white;
   border: 1px solid rgba(191,81,73,1);
}

.clock-in-btn:disabled, .clock-out-btn:disabled{
   background: none;
   border: 1px solid var(--stormy);
}

.submit-btn{
   color: rgba(118,201,76,1);
   border: 1px solid rgba(118,201,76,1);
}

.submit-btn:hover{
   background-color: rgba(118,201,76,1);
   color:white;
}

.cancel-btn{
   color: rgba(191,81,73,1);
   border: 1px solid rgba(191,81,73,1);
}

.cancel-btn:hover{
   background-color:rgba(191,81,73,1);
   color:white;
}

.apply-btn{
   max-height: 55px;
   background: linear-gradient(90deg, rgba(25,34,49,.9) 1%, rgba(73,78,107,1) 100%, rgba(152,135,143,1) 100%);
}

.quick-message{
   color: var(--sunset);
   text-align: center;
}

.report-filters{
   display: flex;
   flex-direction: row;
   max-height: 45px;
   background-color: white;
   border-radius: 5px;
}

.header button{
   font-size: 14px;
   margin-right: 10px;
   margin-top: 5px;
   margin-bottom: 5px;
}

#new-employee-btn, #logout-btn{
   color: var(--teal);
   border: none;
   border-radius: 0px;
}

#new-employee-btn:hover, #logout-btn:hover{
   border: none;
   color: var(--evening);
   background: var(--teal);
   border-radius: 0px;
}
